import { request } from 'utils';

const prefix = '/api/categories';

export const getRoomTypes = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: `/RoomType`,
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createRoomTypes = ({ label }) =>
  request({
    prefix,
    url: `/RoomType`,
    data: {
      label,
    },
    method: 'POST',
  });

export const deleteRoomTypes = ({ id }) =>
  request({
    prefix,
    url: `/RoomType/${id}`,
    method: 'DELETE',
    data: { id },
  });

export const getRoomTypesById = ({ id }) =>
  request({
    prefix,
    url: `/RoomType/${id}`,
    method: 'GET',
    params: { id },
  });

export const updateRoomTypes = ({ id, label }) =>
  request({
    prefix,
    url: `/RoomType/${id}`,
    data: {
      id,
      label,
    },
    method: 'PUT',
  });



