import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input } from 'antd';
import { requiredRule } from 'utils/validateRules';
import { getRoomTypesById, createRoomTypes, updateRoomTypes } from 'services/roomType';
import { getQueryParamId } from 'utils/urlUtil';

@Form.create({ name: 'formRoomType' })
class FormGroundworkTraining extends Component {
  state = {
    roomTypeId: false,
    initialValue: {
      id: '',
      label: '',
    },
  };

  componentDidMount = () => {
    if (getQueryParamId()) {
      this.getRoomTypesById(getQueryParamId());
    }
  };

  getRoomTypesById = async id => {
    this.setState({ roomTypeId: id });
    const res = await getRoomTypesById({ id });
    const initialValue = { ...this.state.initialValue, ...res.data };
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { roomTypeId } = this.state;
    try {
      if (!roomTypeId) {
        await createRoomTypes({
          label: values.label,
        });
      } else {
        await updateRoomTypes({
          id: roomTypeId,
          label: values.label,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Tên phòng" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormGroundworkTraining {...props} />
  </Page>
));



